import React from "react";
import { compose } from "@vxmind/vxhelpers";
import { hot } from "react-hot-loader";
import { PageScreen } from "../components/PageScreen";
import { BottomMenuBar, BottomMenuBarItemRenderOptions, BottomMenuBarMenuItem } from "../components/BottomMenuBar";
import { PartyKnowledgeTabContent } from "./PartyKnowledgeTabContent";
import { PartyLessionTabContent } from "./PartyLessionTabContent";
import { PartyBirthdayTabContent } from "./PartyBirthdayTabContent";
import { PartyPaidTabContent } from "./PartyPaidTabContent";

export function PartyHomePage() {

    const menuItems: BottomMenuBarMenuItem[] = [
        {
            title: "党建知识",
            icon: <i className="zi zi_govDhxk"/>,
            renderContent: () => <PartyKnowledgeTabContent/>
        },
        {
            title: "政治生日",
            icon: <i className="zi zi_birthdaycake"/>,
            renderContent: ({ isSelected }: BottomMenuBarItemRenderOptions) => (
                <PartyBirthdayTabContent
                    isSelected={isSelected}
                />
            )
        },
        {
            title: "党费缴纳",
            icon: <i className="zi zi_creditCard"/>,
            renderContent: () => <PartyPaidTabContent/>
        },
        {
            title: "三会一课",
            icon: <i className="zi zi_textbook"/>,
            renderContent: () => (<PartyLessionTabContent/>)
        }
    ];

    return (
        <PageScreen>
            <BottomMenuBar memoryKey="PartyHome" items={menuItems}/>
        </PageScreen>
    );
}

export default compose(
    hot(module)
)(PartyHomePage);
